
*{
    margin: 0;
    padding: 0;
}
.banner{
    display: flex;
    justify-content: space-around;
    height: 40vh;
    background-image: url('../Assets/solid.jpg');
    background-size: cover;
    background-repeat: no-repeat;
}
.left-banner h1{
    font-size: 4rem;
    margin-top: 30px;
}
.left-banner p{
    font-size: 2rem;
    margin: 10px 0;
}
.left-banner p span{
    font-size: 2rem;
    color: #af500f;
}
.left-banner button{
    border-radius: 6px;
    color: white;
    background-color: #181C14;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    border: none;
    padding: 12px 18px;
    font-size: 16px;
    align-items: center;
    cursor: pointer;
}
.right-banner img{
    max-width: 300px;
    cursor: pointer;
}