*{
    margin: 0;
    padding: 0;
}
.gift{
    display: flex;
    margin: 0px 150px;
}
.left-gift img{
    width: 600px
}
.right-gift{
    margin-left: 10%;
    display: flex;
    flex-direction: column;
}
.right-gift h1{
    padding-top: 40%;
    font-size: 4rem;
}
.right-gift p{
    font-size: 2rem;
    margin-bottom: 1rem;
}
.right-gift button{
    font-size: 1.5rem;
    width: 30%;
    padding: 12px 18px;
    border: none;
    color: white;
    background-color: #181C14;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    border-radius: 6px;
    cursor: pointer;
}