*{
    margin: 0;
    padding: 0;
    
}
.about{
    margin: 50px 150px;
    display: flex;
}
.left-about{
    width: 50%;
    display: flex;
    flex-direction: column;
}
.left-about h1{
    font-size: 6rem;
    margin: 1rem;
}
.left-about p{
    font-size: 1.8rem;
    margin:0rem 1rem;
}
.left-about button{
    width: 20%;
    margin: 1rem;
    border-radius: 6px;
    color: white;
    background-color: #181C14;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    border: none;
    padding: 12px 18px;
    font-size: 1.5rem;
    align-items: center;
    cursor: pointer;
}
.right-about{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.right-about img{
    width: 500px;
    cursor: pointer;
}